html {
  box-sizing: border-box;
  font-size: 100%;
  display: flex;
  min-height: 100vh;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffffff;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
}
body.black {
  background: #000000;
}

@media only screen and (min-width: 1000px) {
  .content {
    flex: 3;
  }
}

@media only screen and (min-width: 1000px) {
  aside {
    flex: 1;
  }
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

.black .app-page__menu a {
  color: #969696;
}
.black .app-page__menu a:hover, .black .app-page__menu a.active {
  color: #ffffff;
}
.black .header__lang:hover {
  color: #ffffff;
}
.black .opt-client:hover {
  color: #ffffff;
}

.warning-banner {
  position: fixed;
  right: 50px;
  bottom: 50px;
  pointer-events: none;
}
.warning-banner img {
  display: block;
}

.opt-client {
  position: fixed;
  left: 50px;
  bottom: 50px;
  cursor: pointer;
  color: #969696;
  font-size: 23px;
  transition: 0.3s ease;
}
.opt-client:hover {
  color: #000000;
}

::placeholder {
  color: #969696;
  font-weight: 300;
}

.search {
  position: fixed;
  z-index: 101;
  left: 0;
  top: 0;
  right: 0;
  background: #FFF;
  height: 173px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
  pointer-events: none;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s ease;
}
.search.opened {
  transform: none;
  opacity: 1;
  pointer-events: all;
}
.search__close {
  position: absolute;
  right: 50px;
  top: 50px;
  cursor: pointer;
}
.search__close img {
  display: block;
}
.search__field {
  max-width: 690px;
  width: 100%;
  position: relative;
}
.search__field button {
  outline: none;
  appearance: none;
  border: none;
  cursor: pointer;
  width: 22px;
  height: 22px;
  position: absolute;
  right: 7px;
  top: 50%;
  margin-top: -11px;
  background: transparent;
}
.search__field img {
  display: block;
}
.search__field input {
  width: 100%;
  height: 50px;
  border: none;
  border-bottom: 2px solid #656565;
  font-size: 23px;
  outline: none;
  padding-right: 30px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none;
}
.header__logo {
  cursor: pointer;
  pointer-events: all;
}
.header__logo img {
  display: block;
}
.header__wrapper {
  display: flex;
  align-items: center;
}
.header__icon {
  cursor: pointer;
  padding: 10px;
  pointer-events: all;
}
.header__icon img {
  display: block;
}
.header__lang {
  color: #969696;
  font-size: 23px;
  cursor: pointer;
  margin-right: 30px;
  pointer-events: all;
  transition: 0.3s ease;
}
.header__lang:hover {
  color: #000000;
}

.app-page {
  display: flex;
  flex: 1 1 auto;
}
.app-page__leftside {
  flex: none;
  width: 100%;
  max-width: 482px;
  padding: 0 50px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
}
.app-page__main {
  display: flex;
  flex: 1 1 auto;
  overflow: hidden;
  padding-left: 482px;
  padding-right: 256px;
}
.app-page__rightside {
  flex: none;
  width: 100%;
  max-width: 256px;
  padding: 0 50px;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
}
.app-page__rightside .app-page__menu {
  text-align: right;
}
.app-page__menu {
  padding-top: 223px;
}
.app-page__menu:hover a {
  color: #969696;
}
.app-page__menu a {
  text-decoration: none;
  color: #000000;
  font-size: 23px;
  display: block;
  margin-bottom: 10px;
  transition: 0.3s ease;
}
.app-page__menu a.active {
  color: #000000;
}
.app-page__menu a:hover {
  color: #000000;
}
.app-page__menu a:first-child {
  margin-bottom: 23px;
}

.homepage {
  height: 100vh;
}
.homepage__arrow {
  cursor: pointer;
  position: absolute;
  top: 50%;
  z-index: 99;
}
.homepage__arrow img {
  display: block;
}
.homepage__arrow-prev {
  left: 50px;
}
.homepage__arrow-next {
  right: 50px;
}
.homepage__arrow-next img {
  transform: scale(-1);
}
.homepage .swiper {
  flex: 1 1 auto;
}
.homepage .swiper .swiper-button-prev::after,
.homepage .swiper .swiper-button-next::after {
  display: none;
}
.homepage-product {
  display: flex;
  justify-content: center;
  overflow: hidden;
  background-size: cover;
  background-position: top center;
}
.homepage img {
  display: block;
}

.catalog__main {
  padding-bottom: 100px;
  flex: 1 1 auto;
  max-width: 940px;
}
.catalog__items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  grid-gap: 45px 20px;
  margin-bottom: 55px;
  padding-top: 45px;
}
.catalog__items:first-child {
  padding-top: 223px;
}
.catalog__item {
  text-decoration: none;
  position: relative;
}
.catalog__item:hover .catalog__item-img img {
  transform: scale(1.15) rotate(2deg);
}
.catalog__item-favourite {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  z-index: 5;
}
.catalog__item-favourite:hover img {
  transform: scale(1.1);
}
.catalog__item-favourite img {
  display: block;
  transition: 0.3s ease;
}
.catalog__item-img {
  overflow: hidden;
}
.catalog__item-img img {
  width: 100%;
  display: block;
  transition: 0.4s ease-in-out;
}
.catalog__item-name {
  font-size: 22px;
  color: #ffffff;
}
.catalog__item-cat {
  font-size: 18px;
  color: #969696;
  margin: 15px 0 5px;
}

.product {
  display: flex;
}
.product__image {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  max-width: 614px;
  width: 100%;
  display: flex;
}
.product__slider {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.product__slider img {
  display: block;
  width: 100%;
}
.product__slider .swiper-pagination {
  bottom: 40px;
}
.product__slider .swiper-pagination-bullet {
  border: 2px solid #FFFFFF;
  background: transparent;
  width: 12px;
  height: 12px;
  opacity: 1;
}
.product__slider .swiper-pagination-bullet-active {
  background: #FFF;
}
.product__main {
  padding: 70px;
  padding-left: 120px;
  color: #ffffff;
  flex: 1 1 auto;
  margin-left: 614px;
}
.product__top {
  display: flex;
  align-items: center;
  margin-bottom: 100px;
}
.product__back {
  display: flex;
  align-items: center;
  color: #ffffff;
  text-decoration: none;
  font-size: 23px;
}
.product__back img {
  display: block;
  margin-right: 20px;
}
.product__cat {
  font-size: 29px;
  font-weight: 500;
}
.product__name {
  font-size: 92px;
  font-weight: 500;
  margin: 0;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
}
.product__number {
  font-size: 18px;
  color: #656565;
}
.product__favourite {
  margin-left: 30px;
  cursor: pointer;
}
.product__favourite img {
  display: block;
}
.product__description {
  margin-top: 60px;
  margin-bottom: 100px;
  max-width: 740px;
  width: 100%;
}
.product__description.active .product__description-title img {
  transform: rotate(90deg);
}
.product__description.active .product__description-body {
  display: block;
}
.product__description-title {
  font-size: 42px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
}
.product__description-title img {
  display: block;
  margin-left: 20px;
  transform: rotate(-90deg);
}
.product__description-body {
  padding-top: 20px;
  display: none;
}
.product__description-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
  font-size: 23px;
}
.product__description-item:not(:last-child) {
  border-bottom: 1px solid #656565;
}
.product__description-desc {
  min-width: 245px;
}
.product__more {
  max-width: 740px;
  width: 100%;
}
.product__more-title {
  font-size: 42px;
  font-weight: 500;
}
.product__more-body {
  padding-top: 40px;
}
.product__more-body ul {
  padding: 0;
  padding-left: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
  grid-gap: 5px 15px;
}
.product__more-body ul li {
  flex: 1 1 30%;
  color: #969696;
}
.product__more-body ul li a {
  font-size: 23px;
  color: #969696;
  text-decoration: none;
}
.product__more-body ul li a:hover {
  color: #ffffff;
}

.footer {
  padding: 0 50px;
  background: #000000;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  pointer-events: none;
  opacity: 0;
  transform: translateY(20px);
  transition: 0.5s ease;
}
.footer.opened {
  transform: none;
  opacity: 1;
  pointer-events: all;
}
.footer__desc {
  font-size: 23px;
}
.footer__shops {
  display: flex;
}
.footer__shop {
  display: flex;
  align-items: center;
  margin-right: 85px;
}
.footer__shop:last-child {
  margin-right: 10px;
}
.footer__shop-logo img {
  display: block;
}
.footer__shop-desc {
  margin-left: 20px;
  font-size: 23px;
}
.footer__shop-desc p {
  margin: 0;
}
.footer__map {
  transition: 0.3s ease;
  opacity: 0.7;
}
.footer__map:hover {
  opacity: 1;
}
.footer__map img {
  display: block;
}

#map {
  width: 100%;
}